<template>
	<view class='container padding'>
		<view class="add flex-row-between" @click="add" v-if="fromType==1">
			<text>新增问题反馈</text>
			<image src="../../static/add.png"></image>
		</view>
		<view class="list" v-if="!empty">
			<view class="item padding" v-for="(item,index) in listData" :key="index" @click='lookOrDispose(item)'>
				<view class="row flex-row-between">
					<view class='title'>{{item.feedbackTypeStr}}</view>
					<view class="status flex-row-end">
						<text class="txt">
							{{item.myState==0?'待处理':'已处理'}}
						</text>
						<image src="../../static/right_black.png" class="icon"></image>
					</view>
				</view>
				<view class="time">{{item.createTimeStr}}</view>
			</view>
			<loadMore :status="status" :content-text="textDesc"></loadMore>
		</view>
		<view class="no_data" v-else>
			暂无数据
		</view>
	</view>
</template>

<script>
	import loadMore from "../../components/loadMore/loadMore.vue"
	import {formatTime} from "../../common/tools.js"
	export default {
		data() {
			return {
				textDesc:{contentdown: "加载更多",contentrefresh: "正在加载...",contentnomore: "没有更多数据了"},
				status:"noMore", //可选值：more（loading前）、loading（loading中）、noMore（没有更多了
				empty:false,
				listData:[],
				fromType:1,// 1 是客户和员工，2 管理员
				paramsInfo:{
					pageNum:1,
					pageSize:8
				},
				userInfo:{}
			};
		},
		methods:{
			add(){
				this.perfectNavigateTo({
					url:'/pages/feedback/feedback'
				})
			},
			lookOrDispose(item){
				if(this.fromType==1 || item.myState==1){ // look
					this.perfectNavigateTo({
						url:'/pages/feedback/lookProcess?feedBackId='+item.id
					})
				}else{
					this.perfectNavigateTo({
						url:'/pages/adminControlOrder/operationQuestion?feedBackId='+item.id
					})
				}
			},
			getList(){
				this.request("/feedBack/feedBacklist",this.paramsInfo).then((res)=>{
					if(res.resultCode==0){
						let _data=res.data.page.list
						if(_data.length>0){ //如果有数据
							this.empty=false
							_data.forEach((item)=>{
								switch(item.feedbackType){
									case 1 : item.feedbackTypeStr="信息咨询类";break
									case 2 : item.feedbackTypeStr="改善建议类";break
									case 3 : item.feedbackTypeStr="服务投诉类";break
									case 4 : item.feedbackTypeStr="质量投诉类";break
									case 5 : item.feedbackTypeStr="违规违纪类";break
									default: item.feedbackTypeStr="- -"
								}
								item.createTimeStr=formatTime(item.createTime)
								// todo 问题反馈状态判定
								if(this.userInfo.role==1){ //党支部
									if(item.nodeStep>10 || item.state==1){ //我这边已经处理了
										item.myState=1
									}else{
										item.myState=0
									}
								}else if (this.userInfo.role==2){ //项目组 通过state 判断即可因为是最后都是项目管理员操作
									if(item.state==1){ //项目组已经处理了
										item.myState=1
									}else{
										item.myState=0
									}
								}else{
									if(item.state==0){
										item.myState=0
									}else{
										item.myState=1
									}
								}
							})
							//- - ------------------------------------------
							if(_data.length<this.paramsInfo.pageSize){ //如果有数据但是 小于 每页数量
								this.status="noMore"
							}else{
								this.paramsInfo.pageNum++
								this.status="more"
							}
							this.listData=this.listData.concat(_data)
						}else{ //如果这次没数据
							this.empty=false
							this.status="noMore"
							if(this.paramsInfo.pageNum==1){ //说明开始就没数据
								this.empty=true
							}
						}
					}else{
						uni.showToast({title:res.resultMsg?res.resultMsg:'网络异常,请稍后重试',icon:'none'})
					}
					uni.stopPullDownRefresh()
				})
			},
		},
		onPullDownRefresh(){ //下拉刷新
			this.paramsInfo.pageNum=1
			this.listData=[]
			this.getList()
		},
		onReachBottom(){ //加载更多
			if(this.status!="noMore"){
				this.getList()
			}
		},
		onShow(){
			uni.startPullDownRefresh();
		},
		components:{
			loadMore
		},
		onShareAppMessage(){
			return {
				title:'卫蓝购',//String	是	分享标题	
				path:'/pages/infomation/index',//String	是	页面 path ，必须是以 / 开头的完整路径。	QQ小程序不支持
				imageUrl:"/static/share.png"
			}
		},
		onLoad(params){
			this.fromType=params.fromType
			let _userInfo=JSON.parse(uni.getStorageSync('userInfo'));
			this.userInfo=_userInfo
		}
	}
</script>

<style lang="stylus">
	.container
		.add
			height:112upx;
			background:#fff
			padding:0upx 30upx
			margin-top:30upx
			background:rgba(255,255,255,1);
			box-shadow:0upx 0upx 20upx 0upx rgba(0,0,0,0.05);
			border-radius:16upx;
			text
				font-size:32upx;
				font-family:PingFangSC-Semibold,PingFang SC;
				font-weight:600;
				color:rgba(30,30,30,1);
			image
				width:28upx
				height:28upx
		.list
			margin-top:31upx
			.item
				background:rgba(255,255,255,1);
				box-shadow:0upx 0upx 20upx 0upx rgba(0,0,0,0.05);
				border-radius:16upx;
				&:not(:first-child)
					margin-top:40upx
				.title
					font-size:28upx;
					font-weight:bold;
					color:rgba(30,30,30,1);
					flex:1
				.status
					width:120upx
					.icon
						width:28upx
						height:28upx
						margin-left:20upx
					.txt
						font-size:24upx;
						font-weight:500;
						color:rgba(30,30,30,1);
				.time
					font-size:24upx
					color:#8491A8
					margin-top:20upx
						
</style>
